import Notification from 'core/notification';
import {get_string as getString} from 'core/str';


/**
 * Show success Notification.
 */
 export function showSuccessNotification() {

    // eslint-disable-next-line no-console
    console.log('showSuccessNotification');

    getString('savesuccess', 'local_wb_faq').then(message => {

        Notification.addNotification({
            message,
            type: "success"
        });

        setTimeout(() => {
            let notificationslist = document.querySelectorAll('#user-notifications div.alert');
            const notificatonelement = notificationslist[notificationslist.length - 1];
            notificatonelement.remove();
        }, 5000);

        return;
    }).catch(e => {
        // eslint-disable-next-line no-console
        console.log(e);
    });
}

/**
 * Show error notification.
 */
export function showErrorNotification() {
    getString('saveerror', 'local_wb_faq').then(message => {

        Notification.addNotification({
            message,
            type: "danger"
        });

        setTimeout(() => {
            let notificationslist = document.querySelectorAll('#user-notifications div.alert');
            const notificatonelement = notificationslist[notificationslist.length - 1];
            notificatonelement.remove();
        }, 5000);

        return;
    }).catch(e => {
        // eslint-disable-next-line no-console
        console.log(e);
    });
}
